import React from "react";
import PropTypes from "prop-types";

function GetAvatar(props) {
  // creamos una propiedad de la clase que es la que vamos a usar en varios mtodos para cargar la imagen
  // esto es un manejador de ficheros
  const fr = new FileReader();

  // creamos un React.createRef porque React no gestiona los <input type="file" /> por ello tenemos que gestionarlo por nuestra cuenta
  const myFileField = React.createRef();
  //  myFileField = document.querySelecto('input')

  const uploadImage = (ev) => {
    // cuando pulsamos en la label o en <input type="file" />:
    // 1 se abre la ventana de nuestro ordenador para elegir un fichero
    // 2 cuando la usuaria elije un fichero se ejecuta este mtodo manejador de eventos
    console.log("La usuaria ha abierto la ventana para elegir ficheros");

    // cuando se abre la ventana de nuestro navegador podemos elegir uno o varios ficheros por ello ev.currentTarget.files es una array
    console.log("La usuaria ha elegido los ficheros", ev.currentTarget.files);

    // para este ejercicio nos interesa solo el primero de los ficheros elegidos por la usuaria, por eso ponemos [0]
    // este primer fichero es un objeto con informacin til del fichero como: nombre, ltima modificacin, tamao del fichero...
    // este objeto no tiene informacin privada del ordenador de la usuaria, por ejemplo no tenemos informacin de la carpeta en la que est la imagen
    console.log(
      "El primero de los ficheros elegidos es",
      ev.currentTarget.files[0]
    );

    // compruebo si la usuaria ha elegido al menos un fichero
    if (ev.currentTarget.files.length > 0) {
      // guardo el primer fichero en myFile
      const myFile = ev.currentTarget.files[0];

      // aado un evento load al manejador de ficheros
      // por qu aado un evento, pues porque esto es una accin asncrona, imaginemos que el fichero pesa 5 Gb, el navegador puede tardar unos cuantos segundos en cargar y procesar el fichero, por eso le decimos "navegador, cuando termines de cargar el fichero me ejecutas el mtodo  image"
      fr.addEventListener("load", getImage);

      // le digo al manejador de ficheros que maneje, que cargue el fichero
      fr.readAsDataURL(myFile);
    }
    // si la usuaria no ha elegido ningn fichero y ha puslado en cerrar la ventana de nuestro ordenador, no hago nada
  };

  const getImage = () => {
    // cuando el navegador termina de manejar el fichero se ejecuta este mtodo porque lo hemos indicado en  fr.addEventListener('load',  getImage);

    //  fr guarda informacin til sobre el fichero cargado
    //console.log('Informacin til sobre el fichero cargado', fr);

    //  fr.result contiene los datos del fichero en un formato que se llama base64 que nos vale por que podemos usarlo para pintar una imagen en HTML
    const image = fr.result;

    // aqu hago lifting con los datos del fichero
    // lo que haga el componente madre con esta informacin es otro problema diferente
    props.updateAvatar(image);
  };

  const avatar = props.avatar === "" ? "" : props.avatar;
  return (
    <div>
      <label className="stuffed-titles" htmlFor="photo">
        Imagen de perfil
      </label>
      <div className="false-stuffed">
        <label className="false-stuffed__false-button" htmlFor="photo">
          Aadir imagen
          <input
            type="file"
            ref={myFileField}
            className="hiddenButton"
            onChange={uploadImage}
            name="photo"
            id="photo"
          />
        </label>
        <div
          className="false-stuffed__false-square"
          style={{ backgroundImage: `url(${avatar})` }}
        ></div>
      </div>
    </div>
  );
}

GetAvatar.propTypes = {
  avatar: PropTypes.string.isRequired,
  updateAvatar: PropTypes.func.isRequired,
};

export default GetAvatar;
